@charset "UTF-8";

@font-face {
  font-family: "b-system";
  src:url("../icons/b_icons/b-system.eot");
  src:url("../icons/b_icons/b-system.eot?#iefix") format("embedded-opentype"),
    url("../icons/b_icons/b-system.woff") format("woff"),
    url("../icons/b_icons/b-system.ttf") format("truetype"),
    url("../icons/b_icons/b-system.svg#b-system") format("svg");
  font-weight: normal;
  font-style: normal;
}


[data-icon]:before {
  font-family: "b-system" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "b-system" !important;
  font-style: normal !important;
  font-weight: normal !important;
